import React from 'react'
import FlashCard from './flashcard'

export default function ListFlashCards({flashcards}) {
    return ( 
        <div className='card-grid'>
            {
                flashcards.map(f=>(
                    <FlashCard flashcard={f} key={f.id} />
                )

                )
            }
            
        </div>
    )
}
